import React, { useEffect, useState } from "react";
import "./Course.css";
import axios from "axios";
import Header from "../../Components/Header/Header";
import { BASE_URL } from "../../BASE_SKILL_NEST";
import { useParams } from "react-router-dom";
import Pic from "../../Assets/c-1.jpg";
const Course = () => {
  const [courseData, setCourseData] = useState({});
  const { id } = useParams();
  const getCoursesDataById = async () => {
    await axios
      .get(BASE_URL + "/courses/" + id)
      .then((val) => setCourseData(val.data))
      .catch((e) => console.log(e));
  };
  useEffect(() => {
    getCoursesDataById();
  }, [id]);
  return (
    <div className="courseMain">
      <div className="courseContent">
        <div className="row d-flex align-items-center h-100 px-5">
          <div className="col-md-6">
            {/* <h2 className="h2CEOLeft text-white ">{courseData.courseType}</h2> */}
            <h2 className="h2CEOLeft text-white ">{courseData.title}</h2>
            <strong className="text-white">{courseData.description}</strong>
          </div>
        </div>
      </div>

      <div className="afterContentMain">
        <div className="imgDivSingleCourse row ">
          {courseData.image !== undefined && (
            <img
              className="imgSingleCourse col-md-6  "
              src={BASE_URL + "/" + courseData.image}
              alt=""
            />
          )}
          <div className="d-flex flex-column justify-content-center  col-md-6">
            <h2 className="h2CEOLeft h2MDLeft  text-info fw-bold ">
              {courseData.title}
            </h2>

            {courseData.duration !== undefined && (
              <strong className="text-secondary">
                Duration: {courseData.duration} weeks
              </strong>
            )}

            <strong className="text-secondary">{courseData.description}</strong>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Course;
